import React, { useEffect, useState } from "react";
import useAxios from "../helper/useAxios";
import { useNavigate } from "react-router-dom";

function Statistics() {
  const navigate = useNavigate();
  const [datas, setDatas] = useState<StatisticsType>({
    peopleWithDebt: 0,
    totalDebt: 0,
    allPeople: 0,
  });
  const { getData } = useAxios();
  const backendUrl =
    process.env.REACT_APP_PORT || "https://natobackend.onrender.com";

  interface StatisticsType {
    peopleWithDebt: number;
    totalDebt: number;
    allPeople: number;
  }

  useEffect(() => {
    const getTotalMoney = async () => {
      try {
        const api: StatisticsType = await getData(
          `https://natobackend.onrender.com/statistics`
        );
        setDatas(api);
      } catch (error) {
        console.log(error);
        navigate("/statistics");
      }
    };
    getTotalMoney();
  }, []);

  return (
    <div>
      <button
        onClick={() => navigate("/")}
        type="submit"
        className="border-[1px] bg-[#3498db] p-[10px] rounded-[12px] text-white mt-[10px] ml-[10px]"
      >
         
      </button>
      <div className="mt-[30px] text-center text-[19px]">
        <p>  - {datas.totalDebt}</p>
        <p className="mt-4">
          ,    - {datas.peopleWithDebt}
        </p>
        <p className="mt-4">   - {datas.allPeople}</p>
      </div>
    </div>
  );
}

export default Statistics;
