import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import Input from "../Reusable/form/input";
import { getPersons } from "../store/debts";
import useAxios from "../helper/useAxios";
import { toast } from "react-toastify";
import { useAppSelector, useAppDispatch } from "../store/reduxStore";
import { logOut } from "../store/debts";

const validateSchema = Yup.object({
  name: Yup.string()
    .min(2, "   !")
    .required(" !"),
  surname: Yup.string()
    .min(3, "   !")
    .required(" !"),
  money: Yup.string().required(" !"),
  mobNumber: Yup.string(),
  personInfo: Yup.string(),
  updateInfo: Yup.string(),
});

const UpdatePerson = () => {
  const navigate = useNavigate();
  const { personId } = useParams();
  const { persons } = useAppSelector((state) => state.persons);
  const dispatch = useAppDispatch();
  const [state, setState] = useState(
    persons.filter((p) => p._id.toString() === personId)
  );
  const [submitStatus, setSubmitStatus] = useState(false);
  const { postData } = useAxios();
  const backendUrl =
    process.env.REACT_APP_PORT || "https://natobackend.onrender.com";

  useEffect(() => {
    setState(persons.filter((p) => p._id.toString() === personId));
  }, [persons]);

  const submitHandler = async (values: {
    name: string;
    mobNumber: string;
    personInfo: string;
    updateInfo: string;
    surname: string;
    money: number;
  }) => {
    if (!localStorage.getItem("tokenShop")) {
      dispatch(logOut());
      navigate("/");
      toast.error(" !");
      return;
    }
    setSubmitStatus(true);
    try {
      await postData(`https://natobackend.onrender.com/updatePerson`, {
        ...values,
        id: personId,
      });
      dispatch(getPersons({}));
      navigate("/");
      toast.success(" !");
    } catch (error) {
      toast.error(",  !");
      console.log(error);
    }
    setSubmitStatus(false);
  };
  return (
    <div>
      <button
        onClick={() => navigate(`/person/${personId}`)}
        type="submit"
        className="border-[1px] bg-[#3498db] p-[10px] rounded-[12px] text-white mt-[10px] ml-[10px]"
      >
         
      </button>
      <div>
        {state.length > 0 ? (
          <div className="max-w-[270px] mx-auto mt-[30px]">
            <Formik
              initialValues={{
                name: state[0].name,
                mobNumber: state[0].mobNumber,
                surname: state[0].surname,
                personInfo: state[0].info,
                updateInfo: "",
                money: state[0].money,
              }}
              onSubmit={submitHandler}
              validationSchema={validateSchema}
            >
              <Form>
                <Input
                  label=""
                  name="name"
                  id="name"
                  type="text"
                  placeholder=""
                />
                <Input
                  label=""
                  name="surname"
                  id="surname"
                  type="text"
                  placeholder=""
                />
                <Input
                  label=" "
                  name="personInfo"
                  id="personInfo"
                  type="text"
                  placeholder=" "
                />
                <Input
                  label=""
                  name="money"
                  id="money"
                  type="number"
                  placeholder=""
                />
                <Input
                  label=""
                  name="mobNumber"
                  id="mobNumber"
                  type="number"
                  placeholder=""
                />
                <Input
                  label=" "
                  name="updateInfo"
                  id="updateInfo"
                  type="text"
                  placeholder=" "
                />
                <div className="grid place-content-center">
                  <button
                    disabled={submitStatus}
                    type="submit"
                    className="border-[1px] bg-[#2ecc71] p-[10px] rounded-[12px] text-white"
                  >
                    
                  </button>
                </div>
              </Form>
            </Formik>
          </div>
        ) : (
          <p>,    !</p>
        )}
      </div>
    </div>
  );
};

export default UpdatePerson;
