import { Formik, Form } from "formik";
import * as Yup from "yup";
import Input from "../../Reusable/form/input";
import useAxios from "../../helper/useAxios";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useAppDispatch } from "../../store/reduxStore";
import { getPersons, logOut } from "../../store/debts";
import Select from "../../Reusable/form/Select";
import { useState } from "react";

const validateSchema = Yup.object({
  status: Yup.string().oneOf(["add", "pay"]).required("!"),
  money: Yup.string().required(" !"),
  info: Yup.string(),
});

const EachPersonForm: React.FC<{ id: string }> = ({ id }) => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { postData } = useAxios();
  const [submitStatus, setSubmitStatus] = useState(false);
  const backendUrl =
    process.env.REACT_APP_PORT || "https://natobackend.onrender.com";

  const submitHandler = async (values: {
    info: string;
    status: string;
    money: string;
  }) => {
    if (!localStorage.getItem("tokenShop")) {
      dispatch(logOut());
      navigate("/");
      toast.error(" !");
      return;
    }

    setSubmitStatus(true);

    const url =
      values.status === "add"
        ? `https://natobackend.onrender.com/addMoney`
        : `https://natobackend.onrender.com/payMoney`;

    try {
      await postData(url, { money: +values.money, id: id, info: values.info });
      dispatch(getPersons({}));
      navigate("/");
      toast.success(" ! :)");
    } catch (error) {
      toast.error(",  !");
      console.log(error);
    }

    setSubmitStatus(false);
  };

  return (
    <Formik
      initialValues={{
        status: "",
        info: "",
        money: "",
      }}
      onSubmit={submitHandler}
      validationSchema={validateSchema}
    >
      <Form>
        <Select label="    ?" name="status" id="status">
          <>
            <option value=""></option>
            <option value="add"> </option>
            <option value="pay"> </option>
          </>
        </Select>
        <Input
          label=""
          name="money"
          id="money"
          type="number"
          placeholder=""
        />
        <Input
          label=" "
          name="info"
          id="info"
          type="text"
          placeholder=" "
        />
        <div className="grid place-content-center">
          <button
            disabled={submitStatus}
            type="submit"
            className="border-[1px] bg-[#2ecc71] p-[10px] px-[15px] rounded-[12px] text-black"
          >
            
          </button>
        </div>
      </Form>
    </Formik>
  );
};

export default EachPersonForm;
