import { Formik, Form } from "formik";
import * as Yup from "yup";
import Input from "../../Reusable/form/input";
import useAxios from "../../helper/useAxios";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useState } from "react";
import { useAppDispatch } from "../../store/reduxStore";
import { logOut } from "../../store/debts";
import { getPersons } from "../../store/debts";

interface PersonType {
  name: string;
  surname: string;
  money: string;
  personInfo: string;
  debtInfo: string;
  mobNumber?: string;
}
const validateSchema = Yup.object({
  name: Yup.string()
    .min(2, "   !")
    .required(" !"),
  surname: Yup.string()
    .min(3, "   !")
    .required(" !"),
  money: Yup.string().required(" !"),
  mobNumber: Yup.string(),
  personInfo: Yup.string(),
  debtInfo: Yup.string(),
});

const AddFormSchema: React.FC<{
  histroyStatus: { status: string; id: string };
  name: string;
  mobNumber: string;
  surname: string;
  info: string;
}> = ({ histroyStatus, name, mobNumber, surname, info }) => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { postData } = useAxios();
  const [submitStatus, setSubmitStatus] = useState(false);
  const backendUrl =
    process.env.REACT_APP_PORT || "https://natobackend.onrender.com";

  const submitHandler = async (values: PersonType) => {
    if (!localStorage.getItem("tokenShop")) {
      dispatch(logOut());
      navigate("/");
      toast.error(" !");
      return;
    }
    setSubmitStatus(true);
    try {
      await postData(`https://natobackend.onrender.com/addPerson`, {
        ...values,
        name: values.name.trim(),
        surname: values.surname.trim(),
        money: +values.money,
        histroyStatus: histroyStatus,
      });
      // info: values.info?.trim(),
      dispatch(getPersons({}));
      navigate("/");
      toast.success(" !");
    } catch (error) {
      toast.error(",  !");
      console.log(error);
    }
    setSubmitStatus(false);
  };

  return (
    <Formik
      initialValues={{
        name: name,
        mobNumber: mobNumber,
        surname: surname,
        personInfo: info,
        debtInfo: "",
        money: "",
      }}
      onSubmit={submitHandler}
      validationSchema={validateSchema}
    >
      <Form>
        <Input
          label=""
          name="name"
          id="name"
          type="text"
          placeholder=""
        />
        <Input
          label=""
          name="surname"
          id="surname"
          type="text"
          placeholder=""
        />
        <Input
          label=" "
          name="personInfo"
          id="personInfo"
          type="text"
          placeholder=" "
        />
        <Input
          label=""
          name="money"
          id="money"
          type="number"
          placeholder=""
        />
        <Input
          label=" "
          name="debtInfo"
          id="debtInfo"
          type="text"
          placeholder=" "
        />
        <Input
          label=""
          name="mobNumber"
          id="mobNumber"
          type="number"
          placeholder=""
        />
        <div className="grid place-content-center">
          <button
            disabled={submitStatus}
            type="submit"
            className="border-[1px] bg-[#2ecc71] p-[10px] rounded-[12px] text-white"
          >
            
          </button>
        </div>
      </Form>
    </Formik>
  );
};
export default AddFormSchema;
